import * as React from 'react'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'
import IconDropdown from "../../../../assets/Icons/IconDropdown/IconDropdown"

function StatusSelect({ activeStatus, applicationId, statuses, width, onPatch }) {
  const handleChange = async (event) => {
    const valueToSet = event.target.value
    const data = { status: valueToSet }
    try {
      const response = await onPatch(applicationId, data)
      // setStatus(valueToSet)
      return response
    } catch (e) {
      return e
    }
  }

  return (
    <Select
      value={activeStatus}
      onChange={handleChange}
      IconComponent={IconDropdown}
      sx={{
        width,
        maxHeight: 40,
        '& .MuiInputBase-input': {
          backgroundColor: statuses?.find((s) => s.label === activeStatus)?.color,
          fontFamily: 'YS Text',
          fontSize: 14,
          padding: '8px 11px ',
          color: '#1a1b22',
        },
        '& .MuiSelect-icon': {
          right: 12,
          top: 'calc(50% - 0.7em)',
        },
        '& .Mui-focused': {
          border: '0 !important',
          color: 'red !important',
        },
      }}
    >
      {statuses?.map((option) => (
        <MenuItem key={option.label} value={option.label}>
          {option.label}
        </MenuItem>
      ))}
    </Select>
  )
}

export default StatusSelect
