import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { IconButton } from '@material-ui/core'
import Tooltip from '@mui/material/Tooltip'
import StatusSelect from '../../../components/Sub/DataTable/StatusSelect/StatusSelect'
import StudentInfoAvatar from '../../../components/Sub/DataTable/StudentInfoAvatar/StudentInfoAvatar'
import { IconCharts, IconDiary, IconFeed } from '../../../assets/Icons'
import { getDurationInYearWeekDay } from '../../../utils/dates'
import CourseInfo from '../../../components/Sub/DataTable/CourseInfo/CourseInfo'

export const getStudentsColumns = (
  patchStatus,
  courses,
  handleFeedClick,
  handleDiaryClick,
  handleMetricsClick,
  studentsStatuses,
) => [
  { field: 'id', headerName: '#', width: 50 },
  {
    field: 'studentInfo',
    headerName: '',
    width: 222,
    renderCell: (params) => (
      <StudentInfoAvatar
        firstName={params.row?.first_name}
        lastName={params.row?.last_name}
        avatar={params.row?.image}
      />
    ),
  },
  {
    field: 'status',
    headerName: '',
    width: 188,
    renderCell: (params) => (
      <StatusSelect
        onPatch={patchStatus}
        applicationId={params.row?.id}
        activeStatus={params.value}
        statuses={studentsStatuses}
        width={188}
      />
    ),
  },
  {
    field: 'course',
    headerName: '',
    width: 104,
    renderCell: (params) => <CourseInfo course={courses?.find((c) => c.id === params.value)?.name} />,
  },
  {
    field: 'live_time',
    headerName: '  -',
    width: 144,
    valueFormatter: (params) => getDurationInYearWeekDay(params?.value),
    sortable: false,
  },
  {
    field: 'response',
    headerName: '',
    width: 80,
    align: 'center',
    valueFormatter: (params) => params?.value?.response,
  },
  {
    field: 'test_task',
    headerName: '',
    width: 80,
    align: 'center',
    valueFormatter: (params) => params?.value?.test_task,
  },
  {
    field: 'tech_interview',
    headerName: '. .',
    width: 80,
    align: 'center',
    valueFormatter: (params) => params?.value?.tech_interview,
  },
  {
    field: 'offer',
    headerName: '',
    width: 80,
    align: 'center',
    valueFormatter: (params) => params?.value?.offer,
  },

  {
    field: '_',
    headerName: ' ',
    width: 202,
    sortable: false,
    disableReorder: true,
    renderHeader: () => (
      <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%', position: 'relative' }}>
        <span>&nbsp;</span>
      </div>
    ),
    renderCell: (params) => (
      <ul
        style={{
          padding: 0,
          margin: '0 0 0 17px',
          listStyle: 'none',
          display: 'flex',
        }}
      >
        <li>
          <ButtonHover
            params={params}
            icon={<IconCharts />}
            title=" "
            onClick={() =>
              handleMetricsClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
                profile: params.row,
              })
            }
          />
        </li>
        <li>
          <ButtonHover
            params={params}
            icon={<IconDiary />}
            title=" "
            onClick={() =>
              handleDiaryClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
              })
            }
          />
        </li>
        <li>
          <ButtonHover
            params={params}
            icon={<IconFeed />}
            title=" "
            onClick={() =>
              handleFeedClick({
                studentName: `${params.row?.first_name} ${params.row?.last_name}`,
                studentId: params.row?.id,
              })
            }
          />
        </li>
      </ul>
    ),
  },
]

const useButtonClasses = makeStyles(() => ({
  iconContainer: {
    padding: 8,
    '&:hover $icon': {
      fill: '#1d6bf3',
    },
  },
  icon: {
    fill: '#797981',
  },
}))

const ButtonHover = ({ icon, title, disabled, onClick }) => {
  const classes = useButtonClasses()
  return (
    <Tooltip title={title}>
      <IconButton className={classes.iconContainer} disabled={disabled} onClick={onClick}>
        {React.cloneElement(icon, { className: classes.icon })}
      </IconButton>
    </Tooltip>
  )
}
