import api from '../../../../api/api'
import { API_STUDENT_BY_ID_ENDPOINT } from '../../../../utils/constants'

const patchStudentStatus = async (studentId, status, setIsLoading, studentsState, accelerationData, setOpen) => {
  const [students, setStudents] = studentsState
  try {
    setIsLoading(true)
    const response = accelerationData
      ? await api.patch(API_STUDENT_BY_ID_ENDPOINT(studentId), { status: status.status, modified_at: accelerationData })
      : await api.patch(API_STUDENT_BY_ID_ENDPOINT(studentId), status)
    const updatedRow = response.data
    //  
    updatedRow.course = updatedRow.course.id
    const newStudents = [...students]
    newStudents[newStudents.findIndex((el) => el.id === updatedRow.id)] = {
      ...updatedRow,
      response: updatedRow?.student_stats?.response,
      test_task: updatedRow?.student_stats?.test_task,
      tech_interview: updatedRow?.student_stats?.tech_interview,
      offer: updatedRow?.student_stats?.offer,
    }
    setStudents(newStudents)
    if (setOpen) setOpen(false)
    return Promise.resolve(response.data)
  } catch (e) {
    return Promise.reject(e.response)
  } finally {
    setIsLoading(false)
  }
}

export default patchStudentStatus
