import React, { FC } from 'react'
import { Box, Divider, Typography, ThemeProvider, Collapse } from '@mui/material'
import useStyles from './StatisticsTrendsComponent.style'
import theme from '../../../utils/themeMUI5'
import IconTrend from '../../../assets/Icons/IconTrend/IconTrend'
import StatisticsCell from './StatisticsCell'
import StatisticsList from './StatisticsList'
import { IStatisticItem } from '../../../services/types/statisticsTrends/statistics-trends-types'
import { useAppSelector } from '../../../services/store/hooks'

interface Props {
  statistics: Array<IStatisticItem>
}

const StatisticsTrendsComponent: FC<Props> = ({ statistics }) => {
  const classes = useStyles()
  const { isHidden } = useAppSelector((store) => store.statisticsMetrics)

  return (
    <ThemeProvider theme={theme}>
      <Box>
        <Collapse in={!isHidden}>
          <Box className={classes.root}>
            <StatisticsList statistics={statistics} />
          </Box>
        </Collapse>
      </Box>
    </ThemeProvider>
  )
}

export default React.memo(StatisticsTrendsComponent)
