import { useState, useEffect, useCallback } from 'react'
import { useNavigate } from 'react-router-dom'
import useAsync from '../../../hooks/useAsync'
import useFilters from '../../../hooks/useFilters'
import { getStudentsColumns } from './getStudentColumns'
import getStudentProfile from '../api/getStudentProfile'
import patchStudentStatus from './api/patchStudentStatus'
import getStudents from './api/getStudents'
import getFilters from '../api/getFilters'
import {
  ACCELERATION_IN_POPUP_DATA,
  ACCELERATION_OUT_POPUP_DATA,
  API_GET_FILTERS_STUDENTS,
} from '../../../utils/constants'
import createStatusesArray from '../../../utils/create-statuses-array'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { setProfile } from '../../../services/slices/auth-slice'
import getStatistics from './api/getStatistics'
import { changeStatisticsMetricsState } from '../../../services/slices/statistics-metrics-slice'

const defaultStudent = { id: undefined }

function useStudentList() {
  const dispatch = useAppDispatch()
  const { isHidden } = useAppSelector((store) => store.statisticsMetrics)
  const [editPopupOpen, setEditPopupOpen] = useState(false)
  const [exportDialogOpen, setExportDialogOpen] = useState(false)
  const [isJobModalOpen, setIsJobModalOpen] = useState(false)
  const [studentToEdit, setStudentToEdit] = useState(defaultStudent)
  const [filtersJSON, setFiltersJSON] = useState({})
  const [students, setStudents] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [isVacationModalOpen, setIsVacationModalOpen] = useState(false)
  const [isSuccessVacation, setIsSuccessVacation] = useState(false)
  const [isOpenAccelerationPopup, setIsOpenAccelerationPopup] = useState(false)
  const [accelerationPopupData, setAccelerationPopupData] = useState(null)
  const [statistics, setStatistics] = useState([])
  const navigate = useNavigate()
  const hook = useFilters()
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [rowsCount, setRowsCount] = useState(undefined)

  const patchStatus = (studentId, status) => {
    const thisStudent = students.find((student) => student.id === studentId)
    if (
      thisStudent?.status === '' &&
      status?.status !== ' ' &&
      status?.status !== ' '
    ) {
      setStudentToEdit(thisStudent)
      setIsOpenAccelerationPopup(true)
      setAccelerationPopupData(
        ACCELERATION_OUT_POPUP_DATA((accelerationData) =>
          patchStudentStatus(
            studentId,
            status,
            setIsLoading,
            [students, setStudents],
            accelerationData,
            setIsOpenAccelerationPopup,
          ),
        ),
      )
      return studentId
    }
    if (status.status === ' ') {
      setIsJobModalOpen(true)
      setStudentToEdit({ id: studentId })
      return studentId
    }
    if (status.status === ' ') {
      setIsVacationModalOpen(true)
      const choosenStudent = students.find((item) => item.id === studentId)
      setStudentToEdit({ ...choosenStudent })
      return choosenStudent.id
    }
    if (status.status === '') {
      setStudentToEdit(thisStudent)
      setAccelerationPopupData(
        ACCELERATION_IN_POPUP_DATA((accelerationData) =>
          patchStudentStatus(
            studentId,
            status,
            setIsLoading,
            [students, setStudents],
            accelerationData,
            setIsOpenAccelerationPopup,
          ),
        ),
      )
      setIsOpenAccelerationPopup(true)
      return studentId
    }

    return run(patchStudentStatus(studentId, status, setIsLoading, [students, setStudents]))
  }

  const handleFeedClick = (studentData) => {
    navigate('/consultant/feed', { state: studentData })
  }

  const handleDiaryClick = (studentData) => {
    navigate('/consultant/diary', { state: studentData })
  }

  const handleMetricsClick = (studentData) => {
    navigate('/consultant/metrics', { state: studentData })
    dispatch(setProfile(studentData.profile))
  }

  useEffect(() => run(getFilters(setFiltersJSON, API_GET_FILTERS_STUDENTS)), [])

  useEffect(() => {
    const { hideMetrics } = JSON.parse(localStorage.getItem('metrics'))
    dispatch(changeStatisticsMetricsState(hideMetrics))
  }, [])

  useEffect(() => {
    if (hook.filters.start_date) {
      run(getStudents(setIsLoading, setStudents, hook.filters, setRowsCount))
      if (!isHidden) run(getStatistics(setIsLoading, hook.filters, setStatistics))
    }
  }, [hook.filters])

  useEffect(() => {
    if (!isHidden && hook.filters.start_date) run(getStatistics(setIsLoading, hook.filters, setStatistics))
  }, [isHidden])

  useEffect(() => {
    if (!isJobModalOpen && !editPopupOpen && studentToEdit?.id) {
      run(getStudents(setIsLoading, setStudents, hook.filters, setRowsCount))
      setStudentToEdit(defaultStudent)
    }
  }, [isJobModalOpen, editPopupOpen])

  useEffect(() => {
    if (!isVacationModalOpen && studentToEdit?.id) {
      //      
      if (isSuccessVacation) {
        const newStudents = [...students]
        newStudents[newStudents.findIndex((el) => el.id === studentToEdit.id)].status = ' '
        setStudents(newStudents)
      }
      setStudentToEdit(defaultStudent)
    }
  }, [isVacationModalOpen])

  const handleDownload = useCallback(() => setExportDialogOpen(true), [setExportDialogOpen])

  const handleCellClick = (params, event) => {
    // eslint-disable-next-line no-param-reassign
    event.defaultMuiPrevented = true
    run(getStudentProfile(params?.row, setStudentToEdit, setEditPopupOpen, setIsLoading))
  }

  const columns = getStudentsColumns(
    patchStatus,
    filtersJSON?.courses,
    handleFeedClick,
    handleDiaryClick,
    handleMetricsClick,
    createStatusesArray(filtersJSON?.statuses),
  )

  return {
    editOpenState: [editPopupOpen, setEditPopupOpen],
    exportOpenState: [exportDialogOpen, setExportDialogOpen],
    jobOpenState: [isJobModalOpen, setIsJobModalOpen],
    vacationOpenState: [isVacationModalOpen, setIsVacationModalOpen],
    accelerationOpenState: [isOpenAccelerationPopup, setIsOpenAccelerationPopup],
    isLoading,
    studentToEdit,
    filtersJSON,
    columns,
    students,
    filtersHook: hook,
    handleDownload,
    setIsSuccessVacation,
    accelerationPopupData,
    rowsCount,
    statistics,
    handleCellClick,
  }
}

export default useStudentList
