import { v4 as uuidv4 } from 'uuid'
import api from '../../../../api/api'
import { API_CONSULTANTS_STUDENT_STATISTICS } from '../../../../utils/constants'

const getStatistics = async (setIsLoading, filters, setStatistics) => {
  try {
    setIsLoading(true)
    const response = await api.get(API_CONSULTANTS_STUDENT_STATISTICS, {
      params: {
        ...filters,
        profile_id: undefined,
        studentId: undefined,
      },
    })
    const statistics = response.data
    //      
    setStatistics([
      {
        title: '- ',
        value: statistics.student_count,
        dynamicValue: statistics.dynamic_student_count,
        id: uuidv4(),
      },
      {
        title: 'Avg ',
        subtitle: ' ',
        value: statistics.vacancy_card_weekly_count,
        dynamicValue: statistics.dynamic_vacancy_card_weekly_count,
        id: uuidv4(),
      },
      {
        title: 'Avg ',
        subtitle: ' ',
        value: statistics.vacancy_card_interviews_weekly_count,
        dynamicValue: statistics.dynamic_interviews_weekly_count,
        id: uuidv4(),
      },
      {
        title: 'Avg lifetime',
        subtitle: ' ',
        value: statistics.avg_live_time_count,
        dynamicValue: statistics.dynamic_avg_live_time_count,
        id: uuidv4(),
      },
      {
        title: '  -',
        value: statistics.vacancy_card_before_find_work_count,
        dynamicValue: statistics.dynamic_vacancy_card_before_find_work_count,
        id: uuidv4(),
      },
      {
        title: 'Avg ER ',
        value: statistics.avg_er_all,
        dynamicValue: statistics.dynamic_avg_er_all,
        id: uuidv4(),
      },
    ])
    return Promise.resolve(response.data)
  } catch (e) {
    return Promise.reject(e.response)
  } finally {
    setIsLoading(false)
  }
}

export default getStatistics
