import api from '../../../api/api'
import { API_STUDENT_BY_ID_ENDPOINT } from '../../../utils/constants'

const getStudentProfile = async (student, setStudentToEdit, setEditPopupOpen, setIsLoading) => {
  try {
    setIsLoading(true)
    const response = await api.get(API_STUDENT_BY_ID_ENDPOINT(student.id))
    setStudentToEdit({ ...response.data, id: student.id })
    setEditPopupOpen(true)
  } catch (e) {
    setStudentToEdit({})
  } finally {
    setIsLoading(false)
  }
}

export default getStudentProfile
