import React, { FC } from 'react'
import { Box, Divider } from '@mui/material'
import { MoonLoader } from 'react-spinners'
import StatisticsCell from './StatisticsCell'
import useStyles from './StatisticsTrendsComponent.style'
import { IStatisticItem } from '../../../services/types/statisticsTrends/statistics-trends-types'

interface Props {
  statistics: Array<IStatisticItem>
}

const StatisticsList: FC<Props> = ({ statistics }) => {
  const classes = useStyles()
  return (
    <ul className={classes.list}>
      {statistics?.length ? (
        statistics.map((item, index) => (
          <Box sx={{ display: 'flex', alignItems: 'center' }} key={item.id}>
            <StatisticsCell item={item} />
            {index !== statistics.length - 1 && (
              <Divider orientation="vertical" variant="middle" flexItem sx={{ margin: '22px 0' }} />
            )}
          </Box>
        ))
      ) : (
        <Box sx={{ width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <MoonLoader color="#1d6bf3" loading size={80} />
        </Box>
      )}
    </ul>
  )
}

export default StatisticsList
