import React, { FC } from 'react'
import { Box, Typography } from '@mui/material'
import IconTrend from '../../../assets/Icons/IconTrend/IconTrend'
import useStyles from './StatisticsTrendsComponent.style'
import { IStatisticItem } from '../../../services/types/statisticsTrends/statistics-trends-types'

interface Props {
  item: IStatisticItem
}
const isNumber = (value: string | number | null): boolean => typeof value === 'number'

const getValue = (value: string | number | null): string | number | null =>
  isNumber(value) ? Math.round(Number(value)) : value

const getTypeOfIcon = (value: string | number | null): 'down' | 'neutral' | 'up' => {
  if (value && value.toString().includes('-')) {
    return 'down'
  }
  if (value === 0) {
    return 'neutral'
  }
  return 'up'
}

const getDynamicColor = (value: string | number | null): string => {
  if (value && value.toString().includes('-')) {
    return '#FF0200'
  }
  if (value === 0) {
    return '#B5B5B7'
  }
  return '#00BA3E'
}

const StatisticsCell: FC<Props> = ({ item }) => {
  const classes = useStyles()
  return (
    <li className={classes.item}>
      <Box className={classes.text}>
        <Typography variant="body4" sx={{ fontWeight: 400 }}>
          {item.title}
        </Typography>
        {item.subtitle && (
          <Typography variant="caption2" sx={{ color: '#797981' }}>
            {`(${item.subtitle})`}
          </Typography>
        )}
      </Box>
      <Box className={classes.trend}>
        <Typography
          variant="h1"
          sx={{
            marginRight: '15px',
            maxWidth: '130px',
            overflow: 'hidden',
            whiteSpace: 'nowrap',
          }}
        >
          {getValue(item.value)}
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
          <IconTrend trend={getTypeOfIcon(item.dynamicValue)} />
          <Typography
            variant="body2"
            sx={{
              fontWeight: 400,
              color: getDynamicColor(item.dynamicValue),
            }}
          >
            {item.dynamicValue === 0 ? `0%` : item.dynamicValue}
          </Typography>
        </Box>
      </Box>
    </li>
  )
}

export default StatisticsCell
