import React, { useState } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { IconButton } from '@material-ui/core'
import { Typography } from '@mui/material'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import { format } from 'date-fns'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useStyles from './AccelerationPopup.style'
import useAsync from '../../../../hooks/useAsync'
import { ControlledDateField, ControlledSelectField } from '../../../Sub/FormElements'
import { accelerationScheme } from '../../../../utils/schemas'
import { formatApiDate } from '../../../../utils/dates'

const AccelerationPopup = ({ openState = [], studentToEdit, popupData }) => {
  const [isOpen, setIsOpen] = openState
  const [modifiedAt, setModifiedAt] = useState(Date.now())

  const { title, subtitle, datePickerText, patch } = popupData || {}

  const { run } = useAsync({
    data: [],
    error: null,
  })

  const defaultValues = {
    modified_at: format(Date.now(), 'yyyy-MM-dd'),
  }

  const {
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm({ defaultValues, resolver: yupResolver(accelerationScheme), mode: 'onChange' })

  const classes = useStyles()

  const handleClose = () => {
    setIsOpen(false)
    reset()
  }

  const dateChangeHandler = (date) => {
    setValue('modified_at', date, { shouldValidate: true, shouldDirty: true })
    setModifiedAt(date)
  }

  const onSubmit = (data) => {
    run(patch(formatApiDate(new Date(data.modified_at))))
  }

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={isOpen} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <Typography className={classes.title}>{title}</Typography>
            <Typography className={classes.subtitle}>{subtitle}</Typography>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-acceleration">
              <ControlledSelectField
                labelTitle=""
                tipContent=" "
                value={studentToEdit?.id}
                items={[{ id: studentToEdit?.id, name: `${studentToEdit?.first_name} ${studentToEdit?.last_name}` }]}
                disabled
              />
              <ControlledDateField
                labelTitle={datePickerText}
                fieldName="modified_at"
                dateValue={modifiedAt}
                error={!!errors.modified_at?.message}
                helperText={errors.modified_at?.message}
                onChange={dateChangeHandler}
              />
            </form>
            <Button form="form-acceleration" className={classes.submitButton} type="submit">
              
            </Button>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(AccelerationPopup)
